* {
  margin: 0;
  box-sizing: border-box;
}

//styling

html {
  font-family: "Nunito Sans", sans-serif;
  font-size: 16px;
  overflow-x: hidden;
  background-color: $black-900;
  color: $white;
}

.top-content {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: $shadow-black-transparent;
  backdrop-filter: blur(0.5rem);
  width: 100%;
  height: 3.5rem;
  padding: 0 1rem;
  position: fixed;
  z-index: 2;
}
#logo,
#dropdown-menu-icon {
  display: block;
  width: 1.75rem;
  height: 1.75rem;
  font-size: 1.75rem;
  transition: ease-in-out 0.2s;
  cursor: pointer;
  &:hover {
    color: $grey-500;
  }
}
#chevron-to-show-social-menu {
  display: none;
}
.nav-desktop {
  display: none;
}

//toggle menu

.toggle-menu {
  display: none;
  position: fixed;
  width: 100%;
  height: auto;
  background-color: $black-600;
  transform: translateY(3.5rem);
  color: $grey-500;

  border-radius: 0 0 5% 5%;
  box-shadow: 1px 3px 15px $shadow-black-transparent;
  opacity: 0;
}

.toggle-menu.active {
  transition: 0.2s ease-in-out;
  opacity: 1;
  display: block;
  z-index: 3;
}

.all-inside {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  flex-direction: row;
  padding: 1rem;
}

.toggle_menu-a {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  flex-direction: column;
  gap: 1rem;
  padding: 1rem 2rem;
  font-size: 1.2rem;
  text-transform: uppercase;
  font-weight: 800;
}
.nav-a-toggle-menu {
  text-decoration: none;
  color: $amulet-green-400;
  transition: 0.1s ease-in-out;
  &:hover {
    /* text-decoration: underline 0.2rem $eastern-blue-500; */
    transform: scale(1.05);
    color: $grey-600;
  }
}

//right side toggle menu
.toggle-column-right {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: auto;
  gap: 0.5rem;
}

#toggle-menu-a-1,
#toggle-menu-a-2,
#toggle-menu-a-3,
#toggle-menu-a-4,
#toggle-menu-a-5,
#toggle-menu-a-6,
#toggle-menu-a-7,
#toggle-menu-a-8,
#toggle-menu-a-9 {
  color: $white;
  font-size: 1.75rem;
  transition: 0.1s ease-in-out;

  &:hover {
    transform: scale(1.2);
    color: $grey-500;
  }
}

/* bottom navigation */
.bottom-navigation-only {
  height: 49.8%;
  width: 100vw;
}

.bottom-nav-ul {
  display: flex;
  justify-content: left;
  align-items: center;
  flex-direction: row;
  list-style: none;
  font-size: 0.9rem;
  padding: 0 1.5rem;
  width: 100vw;
  height: auto;
}

.bottom-li-nav {
  margin: 1rem;
  gap: 1.5rem;
}

#bottom-li-nav-about,
#bottom-li-nav-jobs,
#bottom-li-nav-appren {
  cursor: pointer;
}

#bottom-li-nav-about {
  color: $amulet-green-400;
}
.nav-a-social {
  display: none;
}
