  line-height: 2.8rem;
  text-transform: uppercase;
  width: 100%;
  margin-bottom: 1rem;
  font-weight: 700;
}
//new code
.logo-and-description {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 100%;
  height: auto;
  gap: 0.5rem;
