  line-height: 2rem;
}
//! link styling for artist anchor
.album-link-styling {
  cursor: pointer;
  text-decoration: none;
  position: relative;
  display: inline-block;
  background-color: transparent;
  color: $white;
  transition: color 500ms;
  -webkit-background-clip: text;
  background-clip: text;

  &:hover {
    color: $amulet-green-400;
  }
  &:hover::after {
    transform: scale(1);
  }
}

.album-link-styling::before,
.album-link-styling::after {
  content: "";
  position: absolute;
  height: 2px;
  width: 100%;
  left: 0;
  bottom: 2px;
}
.album-link-styling::before {
  background-color: $amulet-green-400;
}
.album-link-styling::after {
  background-color: $eastern-blue-500;
  transform-origin: left;
  transform: scaleX(0);
  transition: transform 600ms;
}

//end of link styling
