//! checkbox change to custom checkmark

.checkbox-label {
  padding-right: 1rem;
  // styling for the input checkbox
  input {
    position: absolute;
    opacity: 0;
    cursor: pointer;
    height: 0;
    width: 0;
  }
  // styling for the label containing the checkbox
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1rem;
  line-height: 2rem;
  gap: 0.5rem;

  // styling for the custom checkbox
  .checkmark {
    background-color: $amulet-green-400;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 1.4rem;
    width: 1.4rem;
    background-color: $white;
    border-radius: 0.5rem;
    transition: background-color 200ms;
    margin-right: 0.5rem;
    margin-left: 1rem;

    // Hidden checkmark (initially)
    &::after {
      content: "";
      position: absolute;
      display: none;
    }
    &:focus {
      display: none;
      outline: none;
    }
  }

  // styling for the checked checkbox
  input:checked ~ .checkmark {
    background-color: $amulet-green-400;
    border-radius: 0.5rem;
    animation: clicked-checkbox 400ms ease-in-out;

    // display the checkmark when checkbox is checked
    &::after {
      display: block;
      left: 0.5rem;
      top: 0.25rem;
      width: 0.25rem;
      height: 0.6rem;
      border: solid $white;
      border-width: 0 0.15rem 0.15rem 0;
      transform: rotate(45deg);
    }
  }
}
//focus-within only for people who check checkbox with tab key
.user-is-tabbing .checkbox-label:focus-within {
  outline: 0.2rem solid $eastern-blue-500;
}

#label-genre-custom {
  padding-left: 1rem;
}

//checkbox animation

@keyframes clicked-checkbox {
  0% {
    box-shadow: 0 0 0 #a7c89f;
    rotate: 25deg;
  }

  50% {
    rotate: -25deg;
  }

  75% {
    box-shadow: 0 0 0 10px #51634b;
  }

  100% {
    box-shadow: 0 0 0 13px #35482f;
    rotate: 0;
  }
}

